<template>
  <div id="depart_box">
    <div class="flex p20">
      <div class="flex">{{ $route.query.parentDepart }}
        <van-icon name="arrow" />
      </div>
      <div class="flex">{{ $route.query.parent_c_Depart }}
        <van-icon name="arrow" />
      </div>
      <div v-if="departData"
           class="flex">{{ departData.deptName }}
        <van-icon name="arrow" />
      </div>
    </div>
    <div class="depart_content">
      <!-- 用户 -->
      <div v-if="departOtherAll.length > 0"
           class="depar_center">
        <div v-for="(item,i) in departOtherAll"
             :key="i"
             class="flex depart_list space">
          <div class="flex"
               style="align-items:end;">
            <img class="pr12 pt2"
                 src="@/assets/department/depart.png">
            <div>
              <div @click="goNext(item.id)">{{ item.realName }}</div>
            </div>
          </div>
          <div class="flex">
            <img class="pr12 h16"
                 @click="show_operation = true"
                 src="@/assets/department/operation.png">
            <van-icon class="font12"
                      name="arrow" />
          </div>
        </div>
      </div>
      <!-- 子部门 -->
      <div v-if="departOrderAll.length > 0"
           class="depar_center">
        <div v-for="(item,i) in departOrderAll"
             :key="i"
             class="flex depart_list space">
          <div class="flex"
               style="align-items:end;">
            <img class="pr12 pt2"
                 src="@/assets/department/depart.png">
            <div>
              <div @click="goNext(item.id)">{{ item.deptName }}</div>
              <div class="font12">{{ item.orderNum }}人</div>
            </div>
          </div>
          <div class="flex">
            <img class="pr12 h16"
                 @click="show_operation = true"
                 src="@/assets/department/operation.png">
            <van-icon class="font12"
                      name="arrow" />
          </div>
        </div>
      </div>
    </div>
    <div class="flex depart_bottom">
      <div class="w50"
           @click="addDepart">
        <van-icon class="pr12"
                  color="#FF7D41"
                  name="plus" />新建部门
      </div>
      <div class="w50"
           @click="addPerson('/add-departStaff')">
        <van-icon class="pr12"
                  color="#FF7D41"
                  name="plus" />新建人员
      </div>
    </div>
    <!-- 更多操作 -->
    <van-popup v-model="show_operation"
               :close-on-click-overlay='false'
               position="bottom"
               class='operation_box'>
      <div>
        <div v-for="(item,i) in operationAll"
             :key="i"
             class="operation_content"
             :style="{color:i == '1'?'red':''}"
             @click="chooseOperation(i)">
          {{ item }}</div>
      </div>
      <div class="cancel"
           @click="show_operation = false">取消</div>
    </van-popup>
    <!-- 更多操作-编辑部门 -->
    <van-dialog v-model="show_editDepart"
                @cancel='show_editDepart = false'
                title="变更部门"
                show-cancel-button>
      <div class="flex edit_top">
        <div class="w30">员工姓名</div>
        <input v-model="number"
               type="text"
               class="w60 input"
               placeholder="请输入部门名称">
      </div>
      <div class="flex edit_top">
        <div class="w30">所属部门</div>
        <select class="w60"
                style="border:none;"
                v-model="abc"
                @change="chooseDepartment(item)">
          <option v-for="(item,index) in departmentAll"
                  :key="index"
                  :value="item.id">{{ item.name }}</option>
        </select>
      </div>
    </van-dialog>
  </div>
</template>
<script>
import { GetSystemDeptParent } from '@/api/api_depart';
export default {
    data() {
        return {
            show_operation: false,
            show_editDepart: false,
            aaaaa: false,
            departAll: [
                { name: '张三', num: '9' },
                { name: '李四', num: '2' },
                { name: '王五', num: '1' },
                { name: '大笨钟', num: '5' },
                { name: '汪汪汪', num: '10' }
            ],
            operationAll: ['变更部门', '删除员工'],
            departmentAll: [
                { name: '格朗姆科技', id: '1' },
                { name: '成都小住', id: '2' },
                { name: '四川桑瑞', id: '3' },
                { name: '四川研宝', id: '4' }
            ],
            number: null,
            departData: null,
            departOrderAll: [],
            departOtherAll: [],
            abc: null
        };
    },
    created() {
        this.$store.commit('setHeader', '部门管理-3');
        this.getCurrentInfo();
    },
    methods: {
        getCurrentInfo() {
            GetSystemDeptParent({ parentId: this.$route.query.parentId }).done((res) => {
                this.departData = res.data; // 部门
                this.departOtherAll = res.other; // 用户
                this.departOrderAll = res.order; // 子部门
            });
        },
        chooseOperation(i) {
            if (i == '0') {
                this.show_operation = false;
                this.show_editDepart = true;
            } else if (i == '1') {
                alert('删除员工ing');
            }
        },
        addDepart() {
            this.show_editDepart = true;
        },
        addPerson(url) {
            this.$router.push({
                path: url
            });
        },
        chooseDepartment(item) {
        },
        goNext() {
            this.$router.push({
                path: '/staff-management'
            });
        }
    }
};
</script>
<style scoped>
#depart_box {
    color: #000;
    font-size: 0.8rem;
}
.flex {
    display: flex;
    align-items: center;
}
.depart_list {
    border-bottom: 1px solid #efefe4;
    padding: 10px 20px;
    text-align: left;
    line-height: 24px;
}
.depart_bottom {
    justify-content: space-around;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    line-height: 40px;
    background: #fff;
}
.w50 {
    border: 1px solid #efefe4;
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.charger {
    justify-content: space-between;
    padding: 10px 20px;
}
.depar_center {
    background: #fff;
}
.operation_box {
    width: 100%;
    line-height: 45px;
}
.operation_content {
    border-bottom: 1px solid #efefe4;
}
.cancel {
    border-top: 6px solid #f1eff2;
}
.editDepar_box {
    width: 80%;
}
.edit_top {
    padding: 0 16px;
    color: #737373;
    font-size: 0.7rem;
    text-align: left;
    margin: 20px 0;
}
.space {
    justify-content: space-between;
}
.font12 {
    font-size: 0.7rem;
    color: #b2b2b2;
}
.w30 {
    width: 25%;
}
.w60 {
    width: 72%;
    height: 30px;
    line-height: 32px;
    background: #f0eff4;
    text-indent: 1rem;
}
.p20 {
    padding: 20px;
    color: #393939;
}
.h16 {
    height: 16px;
    padding-right: 4px;
}
.pr12 {
    padding-right: 12px;
}
.pt2 {
    padding-top: 2px;
}
/deep/ .van-dialog__confirm {
    color: #ff7d41;
}
</style>



